.containerCoverImage {
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1;
  height: 100vh;
  width: 100%;
  background-image: url("../../../images/home-g0eb7d4997_1920.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.containerPage {
  width: 100%;
  top: 0;
  left: 0;
  z-index: 1;
}

.menu {
  position: fixed;
  top: 0;
  right: 0;
  width: 60px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
  z-index: 999;
  background-color: rgba(57, 75, 65, 0.9019607843);
}
.menu .menu__burger {
  width: 50px;
  height: 6px;
  background-color: #59f9b4;
  border-radius: 5px;
  transition: all 0.05s ease-in-out;
}
.menu .menu__burger:before {
  content: "";
  position: absolute;
  width: 50px;
  height: 6px;
  background: #59f9b4;
  border-radius: 5px;
  transition: all 0.5s ease-in-out;
  transform: translateY(-16px);
}
.menu .menu__burger:after {
  content: "";
  position: absolute;
  width: 50px;
  height: 6px;
  background: #59f9b4;
  border-radius: 3px;
  transition: all 0.5s ease-in-out;
  transform: translateY(16px);
  margin-bottom: 10px;
}

.header {
  position: relative;
  z-index: 1;
  background-color: rgba(255, 255, 255, 0.595);
  width: 100vw;
}
.header .nav {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(57, 75, 65, 0.968627451);
  opacity: 0.9;
  width: 100%;
  top: 0;
  left: 0;
  height: 100vh;
  z-index: 998;
}
.header .nav .nav__list {
  margin: 0;
  height: 100%;
  list-style-type: none;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}
.header .nav .nav__list .nav__item {
  font-size: 1.7rem;
}
.header .nav .nav__list .nav__item .nav__link {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  color: white;
  text-decoration: none;
}

.menu.open .menu__burger {
  transform: translateX(-50px);
  background: transparent;
  box-shadow: none;
}

.menu.open .menu__burger::before {
  transform: rotate(45deg) translate(35px, -35px);
}

.menu.open .menu__burger::after {
  transform: rotate(-45deg) translate(35px, 35px);
}

.main {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

@media screen and (max-width: 600px) {
  .containerCoverImage {
    background-image: url("../../../images/home-g0eb7d4997_1920-mobile.jpg");
    height: 100vh;
    width: 100%;
  }
  .main {
    width: 100%;
    margin: 0;
  }
  .nav__item .nav__link {
    font-size: 1.2rem;
  }
  .menu {
    width: 50px;
    height: 50px;
    box-sizing: border-box;
  }
  .menu .menu__burger {
    width: 35px;
    height: 4px;
  }
  .menu .menu__burger:before {
    content: "";
    width: 35px;
    height: 4px;
  }
  .menu .menu__burger:after {
    width: 35px;
    height: 4px;
  }
  .menu.open .menu__burger {
    transform: translateX(-50px);
    background: transparent;
    box-shadow: none;
  }
  .menu.open .menu__burger::before {
    transform: rotate(45deg) translate(35px, -35px);
  }
  .menu.open .menu__burger::after {
    transform: rotate(-45deg) translate(35px, 35px);
  }
}/*# sourceMappingURL=layout.css.map */