@media screen and (max-width:600px) {
    .educationContainer {
      
    
        .education {
            box-sizing: border-box;
            width:90%;
           padding: 20px;
      
           flex-direction: column;
           justify-content: center;
           align-items: center;
            
       
               
                .educationHeadingh3 {
                   font-size: 1.1rem;
                    width: 90%;
                    text-align: center;
                  margin: 0;
                }
    
            .educationText {
                width:90%;
                article {
    
                .educationTitle {
                 font-size: 0.9rem;
                
                }
                svg {
                    position: relative;
                    top: 3px;
                    height: 16px;
                    width: 16px;
                }
    
                ul {
                  
    
                    .educationTime{
                    font-size: 0.8rem;
                    }
    
                    .educationMoreText{
                       font-size: 0.8rem;
                    }
    
                   
                }
            }
            }
    
        }
    }

}