.workexperienceContainer {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top: 50px;
  margin-bottom: 50px;
}
.workexperienceContainer .workexperience {
  height: 100%;
  width: 60%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  background-color: rgba(57, 75, 65, 0.9019607843);
  border-radius: 5px;
  padding: 20px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: start;
}
.workexperienceContainer .workexperience .workHeadingh3 {
  color: #59f9b4;
  width: 50%;
  text-align: center;
}
.workexperienceContainer .workexperience .workText {
  width: 50%;
  padding: 0;
}
.workexperienceContainer .workexperience .workText ul {
  color: white;
  margin: 0;
  padding: 0;
}
.workexperienceContainer .workexperience .workText ul .workTitle {
  list-style-type: none;
  font-size: 1.4rem;
  margin-top: 5px;
}
.workexperienceContainer .workexperience .workText ul .workInfo {
  list-style-type: none;
  font-style: italic;
}
.workexperienceContainer .workexperience .workText ul .workMoreText {
  list-style-type: none;
  font-style: italic;
}

@media (min-width: 600px) and (max-width: 900px) {
  .workexperienceContainer {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 50px;
    margin-bottom: 50px;
  }
  .workexperienceContainer .workexperience {
    width: 80%;
    box-sizing: border-box;
  }
}
@media screen and (max-width: 600px) {
  .workexperienceContainer .workexperience {
    flex-direction: column;
    width: 90%;
    box-sizing: border-box;
    justify-content: center;
    align-items: center;
  }
  .workexperienceContainer .workexperience .workHeadingh3 {
    font-size: 1.1rem;
    text-align: center;
  }
  .workexperienceContainer .workexperience .workText {
    width: 90%;
    font-size: 0.8rem;
  }
  .workexperienceContainer .workexperience .workText ul .workTitle {
    font-size: 0.8rem;
  }
  .workexperienceContainer .workexperience .workText ul .workTitle svg {
    position: relative;
    top: 3px;
    height: 16px;
    width: 16px;
  }
  .workexperienceContainer .workexperience .workText ul .workInfo {
    font-size: 0.8rem;
  }
  .workexperienceContainer .workexperience .workText ul .workMoreText {
    font-size: 0.8rem;
  }
}/*# sourceMappingURL=workexperience.css.map */