.workexperienceContainer {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-top: 50px;
    margin-bottom:50px;
   
    .workexperience{
        height: 100%;
        width:60%;
        height:fit-content;
        background-color:#394B41E6;
        border-radius: 5px;
        padding:20px;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: start;
    .workHeadingh3 {
        color:#59f9b4;
        width: 50%;
        text-align: center;
    }

    .workText {
      width: 50%;
padding:0;   

      ul {
        color: white;
        margin: 0;
        padding:0;   
  
  
      .workTitle {
        list-style-type: none;
        font-size: 1.4rem;
        margin-top:5px;
      }
      .workInfo {
        list-style-type: none;
        font-style: italic;
      }

     .workMoreText{
      list-style-type: none;
        font-style: italic;
     }
    }
    }
}
}


