
.containerCover {
  position:absolute;
  left: 0;
  top: 0;
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: start;

  .containerPresentation {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    .containerH1 {
      
      background-color: #394B41E6;
      display: flex;
      flex-direction: column;
      justify-content: space-evenly;
      align-items: center;
      width:50%;
      border-radius: 5px;
 
      h1 {
        width: 80%;
        text-align: center;
        margin: 0;
        margin-top:20px;
        padding:10px;
        font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS",
          sans-serif;
        color: #59f9b4;
        font-size: 2rem;
        line-height: 45px;
        text-shadow: 0px 0px 30px white;
      }

      .divider {
        width: 100%;
        height: 50px;
        display: flex;
        justify-content: center;
        align-items: center;
        div {
          width:40%;
          height: 2px;
          background-color: white;
          border-radius: 5px;
        }
      }
     .interestContainer {
      width: 100%;
      display: flex;
      justify-content: space-evenly;
      height: 70px;
      .iconImageContainer {
        position:relative;
        margin:0;
        padding:0;
        width: 70px;
        height: 70px;
        z-index:99;
        cursor:pointer;

        &:hover .intrestIcon{
          display:none;
      
        }
        

        &:hover + .iconText{
        
          display:block;
          color:#59f9b4;
      
        }
       
      .intrestIcon {
        position: relative;
        margin:0;
        padding:0;
        width: 70px;
        height:70px;
      }
    }
      .iconText {
        position:relative;
        margin-top: -60px;
        display:none;
        color:white;
        width:70px;
        height: 90px;
        font-size: 0.9rem;
        text-align: center;
      }
     }

    }
    .imageContainer {
      margin: 0;
      height: 350px;
      width: auto;
   
 
   
      .imgCoverDesktop {
    
        height: 350px;
        width: auto;
        opacity: 0.87;
        border-radius: 5%;
        display: block;;
     
      }
      .imgCoverTablet {
        display:none;
        height: 350px;
        width: auto;
        opacity: 0.87;
        border-radius: 5%;
      
     
      }
    }
  }
}

.buttonMailContainer {

  button {
    margin-bottom:20px;
    padding: 10px 20px 10px 20px;
    border-radius: 15px;
    background-color: white;
    border:none;
    color:#394B41;
    font-size: 1.2rem;
    &:hover {
      background-color: #59f9b4;
    }
  }
}


